import React, { useState, useEffect } from "react";
  
import "./App.css";

import Cat1 from './sleep2.png';
import Cat2 from './radi4.png';
import Drunk1 from './drunk1.png';
import Drunk2 from './drunk2.png';
import Mari from './mari2.png'
import Keyy from './key.png'

const App = () => {
  //  init counter to zero
  const [counter, setCounter] = useState(0)
  const [toggled, setToggled] = useState(true);
  
  const handleMouseDown = () => {
    setCounter(counter + 1)
    setToggled(!toggled)
  }

  const handleMouseUp = () => {
    setToggled(!toggled)
  }

  useEffect(() => {
    window.addEventListener('mousedown', handleMouseDown)
    window.addEventListener('mouseup', handleMouseUp)
    return function cleanup() {
      window.removeEventListener('mousedown', handleMouseDown)
      window.removeEventListener('mouseup', handleMouseUp)
    }
  })

  return (
    <div style={{
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: 'center',
      fontSize: '300%',
      position: 'absolute',
      width: '100%',
      height: '100%',
      top: '-5%',
    }}>
      <div className='image'>
        {toggled ? 
          <img style={{
              position: 'relative',
              top: '30vh'
          }}
            src={Cat1} alt="cat1" /> :
          <img style={{
              position: 'relative',
              top: '30vh'
          }}
            src={Cat2} alt="cat2" />
        }
      </div>
      <div>
        <img src={Drunk1} className="drunk1-logo" alt="drunk1-logo" />
        <img src={Drunk2} className="drunk2-logo" alt="drunk2-logo" />
        <img src={Mari} className="mari1-logo" alt="mari1-logo" />
        <img src={Mari} className="mari2-logo" alt="mari2-logo" />
        <img src={Mari} className="mari3-logo" alt="mari3-logo" />
        <img src={Keyy}  className="key-logo" alt="key-logo" />
      </div>
       <div style={{
         display: 'flex',
         flexDirection: 'column',
         alignItems: 'center',
         justifyContent: 'center',
         fontSize: '150%',
         position: 'relative',
         width: '100%',
         height: '100%',
         top: '-70vh',
       }}>
         POPNANYO
       </div>
      <div style={{
        fontSize: '120%',
        position: 'relative',
        top: '-70vh',
      }}>
        {counter}
      </div>
    </div>
  )
}
  
export default App
